/* DNN Slider Input Style */
div.dnnSliderInput {
	position: relative;
	display: inline-block;
	width: 47%;
  margin:15px 0 23px 0;
  max-width: 445px;
	border: 1px solid #d3d3d3;
	border-radius: 3px;
	height: 10px;
}

div.dnnSliderInput .ui-slider-handle {
	position: absolute;
  z-index: 2;
  width: 14px;
  height: 14px;
  cursor: default;
	border: 1px solid #aaaaaa;
  background-color: #cccccc;
  font-weight: normal;
  color: #555555;
	outline: none;
	top: -2px;
	margin-left: -7px;
	box-sizing: border-box;
}
div.dnnSliderInput .dnnTooltip {
	top: -37px;
	margin-left: -22px;
}
div.dnnSliderInput .dnnTooltip .dnnFormHelpContent {
	visibility: visible;
	padding: 4px 10px;
	width: 25px;
	text-align: center;
	z-index: 1;
}
